//   (base64)
const CHARACTER_IMAGES = [
  `data:image/png;base64,`,
  `data:image/png;base64,`,
  `data:image/png;base64,`,
  `data:image/png;base64,`,
];
const CHARACTER_NAMES = ["", "", "", ""];
// : 0=(), 1=(), 2=(), 3=()
//    
const WRONG_TYPES = [
  { id: 'concept', label: ' ', emoji: '', color: '#667eea' },
  { id: 'calc', label: ' ', emoji: '', color: '#f093fb' },
  { id: 'time', label: ' ', emoji: '', color: '#fa709a' },
  { id: 'trap', label: ' ', emoji: '', color: '#ff6b6b' },
  { id: 'reading', label: ' ', emoji: '', color: '#4facfe' },
  { id: 'memory', label: ' ', emoji: '', color: '#43e97b' },
  { id: 'careless', label: '', emoji: '', color: '#fee140' },
  { id: 'other', label: '', emoji: '', color: '#CD853F' },
];

//     ()
const REVIEW_SCHEDULE = [1, 3, 7, 14, 30];

//   
function getNextReviewDate(wrongAnswer) {
  if (!wrongAnswer.reviewed) return new Date(new Date(wrongAnswer.createdAt).getTime() + REVIEW_SCHEDULE[0] * 86400000);
  const reviewCount = wrongAnswer.reviewCount || 0;
  const scheduleIdx = Math.min(reviewCount, REVIEW_SCHEDULE.length - 1);
  const lastDate = wrongAnswer.lastReviewDate ? new Date(wrongAnswer.lastReviewDate) : new Date(wrongAnswer.createdAt);
  return new Date(lastDate.getTime() + REVIEW_SCHEDULE[scheduleIdx] * 86400000);
}

function isReviewDue(wrongAnswer) {
  const nextDate = getNextReviewDate(wrongAnswer);
  return new Date() >= nextDate;
}

// 
//    1:   (AddWrongAnswer v2)
// 
function AddWrongAnswer({ onSave, onCancel, darkMode, subjects, editData }) {
  const [formData, setFormData] = useState({
    subject: editData?.subject || '',
    questionNumber: editData?.questionNumber || '',
    photos: editData?.photos || [],
    importance: editData?.importance || 2,
    memo: editData?.memo || '',
    wrongTypes: editData?.wrongTypes || [],
    myAnswer: editData?.myAnswer || '',
    correctAnswer: editData?.correctAnswer || '',
  });

  const fileInputRef = useRef(null);
  const cameraInputRef = useRef(null);

  const handlePhotoAdd = (e) => {
    const files = Array.from(e.target.files);
    files.forEach(file => {
      const reader = new FileReader();
      reader.onload = (event) => {
        setFormData(prev => ({ ...prev, photos: [...prev.photos, event.target.result] }));
      };
      reader.readAsDataURL(file);
    });
  };

  const handlePhotoDelete = (index) => {
    setFormData(prev => ({ ...prev, photos: prev.photos.filter((_, i) => i !== index) }));
  };

  const toggleWrongType = (typeId) => {
    setFormData(prev => ({
      ...prev,
      wrongTypes: prev.wrongTypes.includes(typeId)
        ? prev.wrongTypes.filter(t => t !== typeId)
        : [...prev.wrongTypes, typeId]
    }));
  };

  const handleSave = () => {
    if (!formData.questionNumber) { alert('  !'); return; }
    if (formData.photos.length === 0) {
      if (!window.confirm('  ?')) return;
    }
    onSave(formData);
    onCancel();
  };

  return (
    <div className={`App ${darkMode ? 'dark-mode' : 'light-mode'}`}>
      <div className="container">
        <div className="sub-header compact">
          <button className="back-btn" onClick={onCancel}> </button>
          <h1>{editData ? '  ' : '  '}</h1>
        </div>
        <div className="sub-form">
          {/*   */}
          <div className="form-group">
            <label> </label>
            <div className="subject-select-grid">
              {subjects.map(subject => (
                <button key={subject.name}
                  className={`subject-select-btn ${formData.subject === subject.name ? 'active' : ''}`}
                  onClick={() => setFormData({ ...formData, subject: subject.name })}
                  style={{ borderColor: formData.subject === subject.name ? subject.color : 'transparent' }}>
                  {subject.emoji} {subject.name}
                </button>
              ))}
            </div>
          </div>

          {/*   + /  */}
          <div className="form-group">
            <label>  *</label>
            <div className="input-row">
              <div className="input-col">
                <input type="number" placeholder=": 15" value={formData.questionNumber}
                  onChange={(e) => setFormData({ ...formData, questionNumber: e.target.value })}
                  min="1" className="form-input" />
              </div>
              <div className="input-col">
                <input type="text" placeholder="  (: )" value={formData.myAnswer}
                  onChange={(e) => setFormData({ ...formData, myAnswer: e.target.value })}
                  className="form-input" style={{ textAlign:'center' }} />
              </div>
              <div className="input-col">
                <input type="text" placeholder=" (: )" value={formData.correctAnswer}
                  onChange={(e) => setFormData({ ...formData, correctAnswer: e.target.value })}
                  className="form-input" style={{ textAlign:'center' }} />
              </div>
            </div>
          </div>

          {/*    */}
          <div className="form-group">
            <label> ? (  )</label>
            <div className="wrong-type-grid">
              {WRONG_TYPES.map(type => (
                <button key={type.id}
                  className={`wrong-type-btn ${formData.wrongTypes.includes(type.id) ? 'active' : ''}`}
                  onClick={() => toggleWrongType(type.id)}
                  style={{ borderColor: formData.wrongTypes.includes(type.id) ? type.color : 'transparent',
                           background: formData.wrongTypes.includes(type.id) ? `${type.color}22` : undefined }}>
                  {type.emoji} {type.label}
                </button>
              ))}
            </div>
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <div className="importance-buttons">
              {[{ v: 1, label: ' ' }, { v: 2, label: ' ' }, { v: 3, label: ' ' }].map(item => (
                <button key={item.v}
                  className={`importance-btn ${formData.importance === item.v ? 'active' : ''}`}
                  onClick={() => setFormData({ ...formData, importance: item.v })}>
                  {item.label}
                </button>
              ))}
            </div>
          </div>

          {/*   */}
          <div className="form-group">
            <label> </label>
            <div className="photo-buttons">
              <button className="photo-btn" onClick={() => cameraInputRef.current?.click()}>  </button>
              <button className="photo-btn" onClick={() => fileInputRef.current?.click()}>  </button>
            </div>
            <input ref={cameraInputRef} type="file" accept="image/*" capture="environment" onChange={handlePhotoAdd} style={{ display: 'none' }} />
            <input ref={fileInputRef} type="file" accept="image/*" multiple onChange={handlePhotoAdd} style={{ display: 'none' }} />
            {formData.photos.length > 0 && (
              <div className="photo-preview-grid">
                {formData.photos.map((photo, index) => (
                  <div key={index} className="photo-preview-item">
                    <img src={photo} alt={` ${index + 1}`} />
                    <button className="photo-delete-btn" onClick={() => handlePhotoDelete(index)}></button>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/*  */}
          <div className="form-group">
            <label> ()</label>
            <textarea className="form-textarea"
              placeholder="    ...&#10;:    ,  ,  "
              value={formData.memo}
              onChange={(e) => setFormData({ ...formData, memo: e.target.value })}
              rows="3" />
          </div>

          <div className="form-actions">
            <button className="primary-action-btn" onClick={handleSave}> </button>
            <button className="secondary-action-btn" onClick={onCancel}></button>
          </div>
        </div>
      </div>
    </div>
  );
}

// 
//    2:   (ExamHistory)
// 
function ExamHistory({ records, onBack, onDelete, darkMode }) {
  const formatDate = (dateStr) => new Date(dateStr).toLocaleDateString('ko-KR', { year: 'numeric', month: 'long', day: 'numeric', weekday: 'short' });
  const formatTime = (dateStr) => new Date(dateStr).toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
  const getGrade = (score) => {
    if (score >= 95) return '1'; if (score >= 90) return '2'; if (score >= 85) return '3';
    if (score >= 80) return '4'; if (score >= 75) return '5'; if (score >= 70) return '6';
    if (score >= 65) return '7'; if (score >= 60) return '8'; return '9';
  };
  return (
    <div className={`App ${darkMode ? 'dark-mode' : 'light-mode'}`}>
      <div className="container">
        <div className="sub-header">
          <button className="back-btn" onClick={onBack}> </button>
          <h1>  </h1>
        </div>
        {records.length === 0 ? (
          <div className="empty-message" style={{ marginTop: '50px' }}>
            <div style={{ fontSize: '3em', marginBottom: '20px' }}></div>
            <p>   </p>
            <p style={{ fontSize: '0.9em', opacity: 0.6 }}>    </p>
          </div>
        ) : (
          <div className="exam-records-list">
            {records.map((record) => (
              <div key={record.id} className="exam-record-card">
                <div className="exam-record-top">
                  <div>
                    <div className="exam-date-main">{formatDate(record.date)}{record.condition && <span style={{ marginLeft:'8px' }}>{record.condition}</span>}</div>
                    <div className="exam-date-time">{formatTime(record.date)}</div>
                  </div>
                  <button className="delete-btn" onClick={() => onDelete(record.id)}></button>
                </div>
                {record.totalScore && (
                  <div className="exam-total-score">
                    <span className="score-label"></span><span className="score-value">{record.totalScore}</span>
                    {record.percentile && <span className="percentile"> {record.percentile}%</span>}
                  </div>
                )}
                <div className="exam-subjects-grid">
                  {record.subjects && record.subjects.map((subject) => (
                    <div key={subject.name} className="subject-score-card">
                      <div className="subject-name-sm">{subject.emoji} {subject.name}</div>
                      {subject.score ? (<><div className="subject-score-val">{subject.score}</div><div className="subject-grade">{getGrade(subject.score)}</div></>) : (<div className="no-score">-</div>)}
                      {subject.wrongCount && <div className="wrong-count-sm"> {subject.wrongCount}</div>}
                    </div>
                  ))}
                </div>
                {record.memo && (<div className="exam-memo-box"><div className="memo-label"></div><div className="memo-content">{record.memo}</div></div>)}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

// 
//    3:    (ExamResultInput)
// 
function ExamResultInput({ examData, onSave, onCancel, darkMode }) {
  const [formData, setFormData] = useState({
    ...examData,
    subjects: examData.subjects.map(s => ({
      ...s, score: s.score || '', wrongCount: s.wrongCount || '',
      totalQuestions: s.totalQuestions || (s.name === '' ? 20 : s.name.includes('') ? 20 : s.name === '' ? 30 : 45),
    })),
    totalScore: examData.totalScore || '', percentile: examData.percentile || '', memo: examData.memo || ''
  });
  const handleSubjectChange = (index, field, value) => {
    const newSubjects = [...formData.subjects];
    newSubjects[index] = { ...newSubjects[index], [field]: value };
    setFormData({ ...formData, subjects: newSubjects });
  };
  const handleSave = () => {
    const cleanedData = {
      ...formData,
      subjects: formData.subjects.map(s => ({ ...s, score: s.score === '' ? null : parseInt(s.score), wrongCount: s.wrongCount === '' ? null : parseInt(s.wrongCount) })),
      totalScore: formData.totalScore === '' ? null : parseInt(formData.totalScore),
      percentile: formData.percentile === '' ? null : parseInt(formData.percentile)
    };
    onSave(cleanedData);
  };
  return (
    <div className={`App ${darkMode ? 'dark-mode' : 'light-mode'}`}>
      <div className="container">
        <div className="sub-header">
          <button className="back-btn" onClick={onCancel}> </button>
          <h1>  </h1>
        </div>
        <div className="sub-form">
          <p style={{ color: '#999', marginBottom: '20px', fontSize: '0.9em' }}> .     .</p>
          {formData.subjects.map((subject, index) => (
            <div key={subject.name} className="exam-input-section">
              <h3>{subject.emoji} {subject.name}</h3>
              <div className="input-row">
                <div className="input-col"><label></label>
                  <input type="number" placeholder=": 85" value={subject.score}
                    onChange={(e) => handleSubjectChange(index, 'score', e.target.value)} min="0" max="100" className="form-input" /></div>
                <div className="input-col"><label> </label>
                  <input type="number" placeholder={`/${subject.totalQuestions}`} value={subject.wrongCount}
                    onChange={(e) => handleSubjectChange(index, 'wrongCount', e.target.value)} min="0" max={subject.totalQuestions} className="form-input" /></div>
              </div>
            </div>
          ))}
          <div className="exam-input-section"><h3> </h3>
            <div className="input-row">
              <div className="input-col"><label></label><input type="number" placeholder=": 350" value={formData.totalScore} onChange={(e) => setFormData({ ...formData, totalScore: e.target.value })} min="0" max="450" className="form-input" /></div>
              <div className="input-col"><label></label><input type="number" placeholder=": 85" value={formData.percentile} onChange={(e) => setFormData({ ...formData, percentile: e.target.value })} min="0" max="100" className="form-input" /></div>
            </div>
          </div>
          <div className="form-group"><label></label>
            <textarea className="form-textarea" placeholder="   ..." value={formData.memo} onChange={(e) => setFormData({ ...formData, memo: e.target.value })} rows="3" /></div>
          <div className="form-actions">
            <button className="primary-action-btn" onClick={handleSave}> </button>
            <button className="secondary-action-btn" onClick={onCancel}></button>
          </div>
        </div>
      </div>
    </div>
  );
}

// 
//    4:    (ReviewMode v2)
// 
function ReviewMode({ wrongAnswer, currentIndex, total, onComplete, onExit, darkMode }) {
  const progress = ((currentIndex + 1) / total) * 100;
  const nextReview = getNextReviewDate(wrongAnswer);
  return (
    <div className={`App ${darkMode ? 'dark-mode' : 'light-mode'}`}>
      <div className="review-mode-container">
        <div className="review-header">
          <div className="review-progress-text">{currentIndex + 1} / {total}</div>
        </div>
        <div className="review-progress-bar"><div className="review-progress-fill" style={{ width: `${progress}%` }} /></div>
        <div className="review-content">
          <div className="review-info-bar">
            <span className="review-subject-badge">{wrongAnswer.subject}</span>
            <span className="review-question-num">#{wrongAnswer.questionNumber}</span>
            <span className="review-importance">
              {wrongAnswer.importance === 3 && ''}{wrongAnswer.importance === 2 && ''}{wrongAnswer.importance === 1 && ''}
            </span>
          </div>

          {(wrongAnswer.myAnswer || wrongAnswer.correctAnswer) && (
            <div className="answer-comparison">
              {wrongAnswer.myAnswer && <div className="my-answer"> : <strong>{wrongAnswer.myAnswer}</strong></div>}
              {wrongAnswer.correctAnswer && <div className="correct-answer">: <strong>{wrongAnswer.correctAnswer}</strong></div>}
            </div>
          )}

          {wrongAnswer.wrongTypes && wrongAnswer.wrongTypes.length > 0 && (
            <div className="review-type-tags">
              {wrongAnswer.wrongTypes.map(typeId => {
                const type = WRONG_TYPES.find(t => t.id === typeId);
                return type ? <span key={typeId} className="type-tag" style={{ background: `${type.color}22`, borderColor: type.color }}>{type.emoji} {type.label}</span> : null;
              })}
            </div>
          )}

          {wrongAnswer.photos && wrongAnswer.photos.length > 0 ? (
            <div className="review-photos">
              {wrongAnswer.photos.map((photo, index) => (
                <img key={index} src={photo} alt={` ${index + 1}`} className="review-photo"
                  onClick={(e) => { if (e.target.requestFullscreen) e.target.requestFullscreen(); }} />
              ))}
            </div>
          ) : (<div className="no-photo-message">  </div>)}

          {wrongAnswer.memo && (<div className="review-memo-box"><div className="memo-label"></div><div className="memo-content">{wrongAnswer.memo}</div></div>)}

          <div className="review-schedule-info">
            {wrongAnswer.reviewed ? (
              <div className="review-history-badge">
                 {wrongAnswer.reviewCount} 
                {wrongAnswer.lastReviewDate && <span className="last-review">  : {new Date(wrongAnswer.lastReviewDate).toLocaleDateString('ko-KR')}</span>}
                <div style={{ marginTop:'4px', fontSize:'0.85em', opacity:0.8 }}> : {nextReview.toLocaleDateString('ko-KR')}</div>
              </div>
            ) : (
              <div className="review-due-badge">  : {nextReview.toLocaleDateString('ko-KR')}</div>
            )}
          </div>
        </div>
        <div className="review-navigation">
          <button className="action-btn complete-action" onClick={onComplete}> </button>
          <button className="action-btn exit-action" onClick={onExit}> </button>
        </div>
        <div className="review-tips">     </div>
      </div>
    </div>
  );
}

// 
//    5:   (WrongAnswersView v2)
// 
function WrongAnswersView({ wrongAnswers, filter, onFilterChange, onBack, onAdd, onDelete, onEdit, onToggleImportance, onStartReview, onViewSingle, darkMode }) {
  const [showStats, setShowStats] = useState(false);

  const filteredAnswers = wrongAnswers.filter(wrong => {
    if (filter === 'all') return true;
    if (filter === 'unreviewed') return !wrong.reviewed;
    if (filter === 'important') return wrong.importance === 3;
    if (filter === 'due') return isReviewDue(wrong);
    return wrong.subject === filter;
  });

  const formatDate = (dateStr) => {
    const diff = Math.floor((new Date() - new Date(dateStr)) / 86400000);
    if (diff === 0) return ''; if (diff === 1) return '';
    if (diff < 7) return `${diff} `;
    return new Date(dateStr).toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' });
  };

  const unreviewedCount = wrongAnswers.filter(w => !w.reviewed).length;
  const importantCount = wrongAnswers.filter(w => w.importance === 3).length;
  const dueCount = wrongAnswers.filter(w => isReviewDue(w)).length;

  //   
  const typeStats = {};
  const subjectStats = {};
  wrongAnswers.forEach(w => {
    //  
    subjectStats[w.subject] = (subjectStats[w.subject] || 0) + 1;
    //  
    if (w.wrongTypes) w.wrongTypes.forEach(t => { typeStats[t] = (typeStats[t] || 0) + 1; });
  });

  //    TOP 3
  const topTypes = Object.entries(typeStats).sort((a, b) => b[1] - a[1]).slice(0, 3);

  //    ( + 3 )
  const getRepeatWarning = (wrong) => {
    if (!wrong.wrongTypes || wrong.wrongTypes.length === 0) return null;
    const samePattern = wrongAnswers.filter(w =>
      w.id !== wrong.id && w.subject === wrong.subject &&
      w.wrongTypes && w.wrongTypes.some(t => wrong.wrongTypes.includes(t))
    );
    return samePattern.length >= 2 ? samePattern.length + 1 : null;
  };

  return (
    <div className={`App ${darkMode ? 'dark-mode' : 'light-mode'}`}>
      <div className="container">
        <div className="wrong-answers-header">
          <button className="back-btn" onClick={onBack}> </button>
          <h1> </h1>
          <button className="add-wrong-btn" onClick={onAdd}> </button>
        </div>

        {/*    */}
        {wrongAnswers.length > 0 && (
          <button className="stats-toggle-btn" onClick={() => setShowStats(!showStats)}>
            {showStats ? '  ' : '   '}
          </button>
        )}

        {/*    */}
        {showStats && wrongAnswers.length > 0 && (
          <div className="wrong-stats-dashboard">
            <div className="wrong-stats-row">
              <div className="wrong-stat-box">
                <div className="wrong-stat-num">{wrongAnswers.length}</div>
                <div className="wrong-stat-label"> </div>
              </div>
              <div className="wrong-stat-box">
                <div className="wrong-stat-num">{dueCount}</div>
                <div className="wrong-stat-label"> </div>
              </div>
              <div className="wrong-stat-box">
                <div className="wrong-stat-num">{unreviewedCount}</div>
                <div className="wrong-stat-label"></div>
              </div>
            </div>
            {topTypes.length > 0 && (
              <div className="weak-type-section">
                <h4>   </h4>
                <div className="weak-type-list">
                  {topTypes.map(([typeId, count]) => {
                    const type = WRONG_TYPES.find(t => t.id === typeId);
                    return type ? (
                      <div key={typeId} className="weak-type-item" style={{ borderColor: type.color }}>
                        <span>{type.emoji} {type.label}</span><strong>{count}</strong>
                      </div>
                    ) : null;
                  })}
                </div>
              </div>
            )}
            <div className="subject-stats-section">
              <h4>  </h4>
              <div className="subject-stats-bars">
                {Object.entries(subjectStats).sort((a,b) => b[1] - a[1]).map(([name, count]) => (
                  <div key={name} className="subject-stat-row">
                    <span className="subject-stat-name">{name}</span>
                    <div className="subject-stat-bar-bg">
                      <div className="subject-stat-bar-fill" style={{ width: `${(count / wrongAnswers.length) * 100}%` }} />
                    </div>
                    <span className="subject-stat-count">{count}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {/*    */}
        {wrongAnswers.length > 0 && (
          <div className="review-section">
            <h3>  </h3>
            <div className="review-options">
              <button className="review-option-btn" onClick={() => onStartReview('all')}> ({wrongAnswers.length})</button>
              <button className="review-option-btn" onClick={() => onStartReview('due')} disabled={dueCount === 0}>
                   ({dueCount})
              </button>
              <button className="review-option-btn" onClick={() => onStartReview('unreviewed')} disabled={unreviewedCount === 0}>   ({unreviewedCount})</button>
              <button className="review-option-btn important-review" onClick={() => onStartReview('important')} disabled={importantCount === 0}> ({importantCount})</button>
            </div>
          </div>
        )}

        {/*   */}
        <div className="filter-tabs">
          <button className={filter === 'all' ? 'active' : ''} onClick={() => onFilterChange('all')}> ({wrongAnswers.length})</button>
          <button className={filter === 'due' ? 'active' : ''} onClick={() => onFilterChange('due')}>  ({dueCount})</button>
          <button className={filter === 'unreviewed' ? 'active' : ''} onClick={() => onFilterChange('unreviewed')}>   ({unreviewedCount})</button>
          <button className={filter === 'important' ? 'active' : ''} onClick={() => onFilterChange('important')}> ({importantCount})</button>
        </div>

        {/*   */}
        <div className="subject-filter">
          {['all', '', '', '', '', '1', '2'].map(subject => (
            <button key={subject} className={filter === subject ? 'active' : ''} onClick={() => onFilterChange(subject)}>
              {subject === 'all' ? '' : subject}
            </button>
          ))}
        </div>

        {/*    */}
        {filteredAnswers.length === 0 ? (
          <div className="empty-message" style={{ marginTop: '50px' }}>
            <div style={{ fontSize: '3em', marginBottom: '20px' }}></div>
            <p> !</p>
            <p style={{ fontSize: '0.9em', opacity: 0.6 }}>{filter !== 'all' ? '  ' : '    '}</p>
          </div>
        ) : (
          <div className="wrong-answers-list">
            {filteredAnswers.map((wrong) => {
              const repeatCount = getRepeatWarning(wrong);
              const isDue = isReviewDue(wrong);
              return (
                <div key={wrong.id} className={`wrong-answer-card ${isDue ? 'due-card' : ''}`} onClick={() => onViewSingle(wrong)}>
                  {repeatCount && <div className="repeat-warning">   {repeatCount} !</div>}
                  {isDue && !wrong.reviewed && <div className="due-badge-top">  !</div>}
                  <div className="wrong-header">
                    <div className="wrong-info">
                      <span className="subject-badge">{wrong.subject}</span>
                      <span className="question-number">#{wrong.questionNumber}</span>
                      {wrong.myAnswer && wrong.correctAnswer && (
                        <span className="answer-mini">{wrong.myAnswer}{wrong.correctAnswer}</span>
                      )}
                      <span className="wrong-date">{formatDate(wrong.createdAt)}</span>
                    </div>
                    <div className="wrong-actions" onClick={(e) => e.stopPropagation()}>
                      <button className="icon-action-btn" onClick={() => onToggleImportance(wrong.id)}>
                        {wrong.importance >= 3 ? '' : wrong.importance === 2 ? '' : ''}
                      </button>
                      <button className="icon-action-btn" onClick={() => onEdit(wrong)}></button>
                      <button className="icon-action-btn" onClick={() => onDelete(wrong.id)}></button>
                    </div>
                  </div>

                  {/*   */}
                  {wrong.wrongTypes && wrong.wrongTypes.length > 0 && (
                    <div className="card-type-tags">
                      {wrong.wrongTypes.map(typeId => {
                        const type = WRONG_TYPES.find(t => t.id === typeId);
                        return type ? <span key={typeId} className="type-tag-sm" style={{ background: `${type.color}22`, color: type.color }}>{type.emoji} {type.label}</span> : null;
                      })}
                    </div>
                  )}

                  {wrong.photos && wrong.photos.length > 0 && (
                    <div className="wrong-photos-grid">
                      {wrong.photos.map((photo, index) => (<img key={index} src={photo} alt={` ${index + 1}`} className="wrong-photo-thumb" />))}
                    </div>
                  )}
                  {wrong.memo && (<div className="wrong-memo-inline"><span>:</span> {wrong.memo}</div>)}
                  <div className="wrong-footer">
                    {wrong.reviewed ? (
                      <div className="reviewed-badge"> {wrong.reviewCount}</div>
                    ) : (<div className="unreviewed-badge"> </div>)}
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}

